.swiper {
  width: 100%;
}

.swiper-button-prev,
.swiper-button-next {
  top: 21%;

  svg {
    stroke: $brown;
  }
}

.swiper-pagination-fraction {
  width: 100%;
}

.swiper-pagination-horizontal {
  width: 100%;

  .swiper-pagination-bullet {
    color: #9b9997;
    margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 6px);
    cursor: pointer;
  }
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  opacity: var(--swiper-pagination-bullet-opacity, 1);
  color: #1d1613;
}

.swiper-pagination {
  position: absolute;
  bottom: 14.5%;
  text-align: center;
}

.swiper-slide {
  margin: 0 0 25px 0;

  a {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $dark;

    h3 {
      @include font(500, 16px, 21px);
      margin: 19px 0 7px 0;
    }

    p {
      @include font(600, 14px, 18px);
      margin: 0 0 5px 0;
    }

    picture {
      display: flex;
    }

    img {
      width: 100%;
      height: 100%;

      @media (max-width: $desktop) {
        width: 324px;
        height: 340px;
      }

      @media (max-width: $tablet) {
        width: 100%;
        height: 100%;
      }

      @media (max-width: 320px) {
        width: 130px;
        height: 136px;
      }
    }
  }
}


@media (max-width: $desktop) {
  .swiper-slide {
    a {
      h3 {
        margin: 22px 0 7px 0;
      }
    }

    &:nth-child(50n+3) {
      a {
        h3 {
          margin: 22px -14px 7px 0;
        }
      }
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    top: 16.3%;

    svg {
      width: 40px;
      height: 20px;
    }
  }

  .swiper-pagination {
    bottom: 9%;
  }

  .swiper-button-prev {
    left: 5px;
  }

  .swiper-button-next {
    right: 5px;
  }
}

@media (max-width: $tablet) {
  .swiper-slide {
    a {
      h3 {
        text-align: center;
        margin: 16px 0 7px 0;
      }
    }

    &:nth-child(50n+3) {
      a {
        h3 {
          margin: 16px 0 7px 0;
        }
      }
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }

  .swiper-pagination {
    bottom: 7%;
  }
}
