.instagram {
  padding: 0 30px;

  @media (max-width: $desktop) {
    padding: 0 45px 0;
  }

  @media (max-width: $tablet) {
    padding: 0 15px 0;
  }
}

.instagram__wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 0 132px 0;

  @media (max-width: $desktop) {
    padding: 0 0 80px 0;
  }

  @media (max-width: $tablet) {

    padding: 0 0 71px 0;
  }
}


.instagram__title {
  text-align: center;
  margin: 0 0 63px 0;

  h2 {
    font-family: "CormorantGaramond", "Times New Roman", serif;

    @include font(600, 34px, 44px);
    letter-spacing: 0.03em;
    margin: 0 0 32px 0;
  }

  a {
    color: $dark;
    border-bottom: 1px solid $brown;

    &:hover {
      opacity: 0.7;
    }
  }

  @media (max-width: $desktop) {
    margin: 0 0 65px 0;

    & h2 {
      margin: 0 0 20px 0;
    }

    & a {
      padding: 0 0 2px 0;
    }
  }

  @media (max-width: $tablet) {
    margin: 0 0 50px 0;
  }
}

.instagram__container ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 19.66% 34.19% repeat(2, 19.66%);
  grid-template-rows: repeat(2, min-content);
  row-gap: 34px;
  column-gap: 2.31%;
}

.instagram__container li {
  picture {
    display: flex;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &:first-child + li {
    grid-area: 1/2/3/3;

    picture {
      height: 100%;
    }
  }
}

@media (width: 768px) {
  .instagram__container {
    ul {

      grid-template-columns: 19.62% 34.07% repeat(2, 19.62%);
      grid-template-rows: repeat(2, min-content);
      row-gap: 21px;
      column-gap: 16px;

      li:first-child,
      li:nth-child(50n+3),
      li:nth-child(50n+3) + li {
        img {
          width: 133px;
          height: 133px;
        }
      }

      li:first-child + li {
        grid-area: 1/2/3/3;

        img {
          width: 231px;
          height: 240px;
        }
      }

      li:nth-child(50n+5),
      li:nth-child(50n+5) + li,
      li:last-child {
        img {
          width: 133px;
          height: 87px;
        }
      }
    }
  }
}

@media (max-width: $tablet) {
  .instagram__container {
    ul {
      grid-template-columns: repeat(2, 45.86%);
      grid-template-rows: repeat(3, min-content);
      row-gap: 27px;
      column-gap: 24px;
      justify-content: space-between;

      li {
        picture {
          img {
            width: 100%;
            height: 100%;
          }
        }
      }

      li:first-child + li {
        grid-area: 1/2/3/3;
      }

      li:nth-child(50n+5) {
        grid-area: 2/1/3/2;
      }

      li:nth-child(50n+3) {
        display: none;
      }

      li:nth-child(50n+4) {
        display: none;
      }
    }
  }
}

@media (width: 320px) {
  .instagram__container {
    ul {
      li:first-child {
        img {
          width: 133px;
          height: 133px;
        }
      }

      li:first-child + li {
        img {
          width: 133px;
          height: 247px;
        }
      }

      li:nth-child(50n+5),
      li:nth-child(50n+5) + li,
      li:last-child {
        img {
          width: 133px;
          height: 87px;
        }
      }
    }
  }
}
